.player-container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.track-info {
  text-align: center;
  margin-bottom: 20px;
}

.track-info h2 {
  margin: 0;
  font-size: 24px;
}

.track-info h3 {
  margin: 5px 0;
  color: #666;
}

.timeline-container {
  margin: 20px 0;
}

.timeline {
  border-radius: 4px;
  overflow: hidden;
}

.controls {
  display: flex;
  gap: 10px;
  justify-content: center;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  background: #1db954;
  color: white;
  cursor: pointer;
  font-weight: bold;
}

button:hover {
  background: #1ed760;
}

.loop-inputs {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.loop-inputs input[type="range"] {
  width: 100%;
  margin: 10px 0;
} 